//Sliders
$('.works__slider-wrap').slick({
    fade: true,
    infinite: false,
    slidesToShow: 1,
    slidesToScroll: 1,
    swipe: false,
    dots: true,
    prevArrow: $('.works__prev'),
    nextArrow: $('.works__next'),
    appendDots: $('.news__dots'),
    customPaging : function(slider, i) {
        var thumb = $(slider.$slides[i]).data();
        return '0' + (i + 1);
    },
    dotsClass: 'news__dots-list',
});

$('.feedbacks__cards').slick({
    infinite: false,
    slidesToShow: 4,
    slidesToScroll: 1,
    swipe: false,
    dots: true,
    prevArrow: $('.feedbacks__prev'),
    nextArrow: $('.feedbacks__next'),
    appendDots: $('.news__dots2'),
    customPaging : function(slider, i) {
        var thumb = $(slider.$slides[i]).data();
        return '0' + (i + 1);
    },
    dotsClass: 'news__dots-list',
    responsive: [
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 3,
            }
        },
        {
            breakpoint: 769,
            settings: {
                slidesToShow: 2,
            }
        },
        {
            breakpoint: 400,
            settings: {
                slidesToShow: 1,
            }
        },
    ]
});


$(document).ready(function() {

    // Before - After
    $(".work-card__img").twentytwenty({
        default_offset_pct: 0.5,
        no_overlay: true,
    });

    //Quiz
    $('.quiz-head__btn').click(function () {
        $('.quiz-head').css('display','none')
        $('.quiz-body').addClass('quiz-body--active')
        $('.quiz-nav').addClass('quiz-nav--active')
    })

    $('.quiz-next').click(function (e) {
        $('.quiz-prev').addClass('quiz-prev--active')

        let activeStep =$('.quiz-step--active').attr('data-step')

        if (activeStep === '1') {
            if($('.quiz-input:checked').length > 0) {
                $('.quiz-step1').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step1').find('.quiz-error').addClass('quiz-error--active')
                $('.quiz-prev').removeClass('quiz-prev--active')
                return
            }
        }

        if (activeStep === '2') {
            if($('.quiz-input2:checked').length > 0) {
                $('.quiz-step2').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step2').find('.quiz-error').addClass('quiz-error--active')
                return
            }
        }

        if(activeStep === "3"){
            if($('.size-input').val() !== '' && $('.size-input2').val() !== '' ) {
                $('.quiz-step3').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step3').find('.quiz-error').addClass('quiz-error--active')
                return;
            }
        }

        if (activeStep === '4') {
            if($('.quiz-input4:checked').length > 0) {
                $('.quiz-step4').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step4').find('.quiz-error').addClass('quiz-error--active')
                return
            }
        }

        if(activeStep === "5"){
            if($('.address-input').val() !== '' ) {
                $('.quiz-step5').find('.quiz-error').removeClass('quiz-error--active')
            } else{
                $('.quiz-step5').find('.quiz-error').addClass('quiz-error--active')
                return;
            }
        }

        var $current = $('.quiz-step--active');

        $current.removeClass('quiz-step--active');
        $current.next('.quiz-step').addClass('quiz-step--active');
        if (!$current.next('.quiz-step').get(0)) {
            $('.quiz-body').remove()
            $('.quiz-nav').remove()
            $('.end').addClass('end--active')
        }


        if (activeStep === '1') {
            $('.quiz-step').parent('.quiz-body').find('.progress-bar span').css('width', '25%')
            $('.quiz-step').parent('.quiz-body').find('.progress-percentage').html(25)
        }

        if (activeStep === '2') {
            $('.quiz-step').parent('.quiz-body').find('.progress-bar span').css('width', '50%')
            $('.quiz-step').parent('.quiz-body').find('.progress-percentage').html(50)
        }

        if(activeStep === "3"){
            $('.quiz-step').parent('.quiz-body').find('.progress-bar span').css('width', '75%')
            $('.quiz-step').parent('.quiz-body').find('.progress-percentage').html(75)
        }

        if (activeStep === '4') {
            $('.quiz-step').parent('.quiz-body').find('.progress-bar span').css('width', '100%')
            $('.quiz-step').parent('.quiz-body').find('.progress-percentage').html(100)
        }
    })

    $('.quiz-prev').click(function () {

        var $current = $('.quiz-step--active');

        $current.removeClass('quiz-step--active');
        $current.prev('.quiz-step').addClass('quiz-step--active');
        if ( $('.quiz-step').first().hasClass('quiz-step--active') ) {
            $(this).removeClass('quiz-prev--active')
        }

        let activeStep =$('.quiz-step--active').attr('data-step')

        if (activeStep === '1') {
            $('.quiz-step').parent('.quiz-body').find('.progress-bar span').css('width', '0')
            $('.quiz-step').parent('.quiz-body').find('.progress-percentage').html(0)
        }

        if (activeStep === '2') {
            $('.quiz-step').parent('.quiz-body').find('.progress-bar span').css('width', '25%')
            $('.quiz-step').parent('.quiz-body').find('.progress-percentage').html(25)
        }

        if(activeStep === "3"){
            $('.quiz-step').parent('.quiz-body').find('.progress-bar span').css('width', '50%')
            $('.quiz-step').parent('.quiz-body').find('.progress-percentage').html(50)
        }

        if (activeStep === '4') {
            $('.quiz-step').parent('.quiz-body').find('.progress-bar span').css('width', '75%')
            $('.quiz-step').parent('.quiz-body').find('.progress-percentage').html(75)
        }
    })

    $('.end-btn').click(function () {
        $(this).parents('.end').remove()
        $('.quiz-form').css('display', 'block')
    })

    //Input-file
    var inputs = document.querySelectorAll('.quiz__file')

    for (var i = 0, len = inputs.length; i < len; i++) {
        customInput(inputs[i])
    }

    function customInput (el) {
        const fileInput = el.querySelector('[type="file"]')
        const label = el.querySelector('[data-js-label]')

        fileInput.onchange =
            fileInput.onmouseout = function () {
                if (!fileInput.value) return

                var value = fileInput.value.replace(/^.*[\\\/]/, '')
                el.className += ' -chosen'
                label.innerText = value
            }
    }

    //All-text
    $('.feedback-card__btn').click(function () {
        $(this).toggleClass('feedback-card__btn--active')
        $(this).parents('.feedback-card').find('.feedback-card__text').toggleClass('feedback-card__text--active')
    })

    // Animation
    const animItems = document.querySelectorAll('._anim-items')

    if (animItems.length > 0) {
        window.addEventListener('scroll', animOnScroll)
        function animOnScroll(params) {
            for (let index = 0; index < animItems.length; index++) {
                const animItem = animItems[index];
                const animItemHeight = animItem.offsetHeight;
                const animItemOffset = offset(animItem).top;
                const animStart = 4;

                let animItemPoint = window.innerHeight - animItemHeight / animStart;

                if (animItemHeight > window.innerHeight) {
                    animItemPoint = window.innerHeight - window.innerHeight / animStart;
                }

                if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                    animItem.classList.add('active');
                } else {
                    if (!animItem.classList.contains('anim-no-hide')) {
                        animItem.classList.remove('active')
                    }
                }
            }
        }
        function offset(el) {
            const rect = el.getBoundingClientRect(),
                scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
                scrollTop = window.pageYOffset || document.documentElement.scrollTop;
            return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
        }

        setTimeout(() => {
            animOnScroll();
        }, 300)
    }

})



